import { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'
import { PieChart } from '@mui/x-charts/PieChart';
import { BarChart } from '@mui/x-charts/BarChart';

function Group() {
  const [songs, setSongs] = useState({})
  const [currSong, setCurrSong] = useState('')
  const {groupName} = useParams()
  const searchParams = new URLSearchParams({
    'group_name': groupName
  }).toString()

  useEffect(() => {
    fetch('https://brygk8u8b9.execute-api.us-east-1.amazonaws.com/dev?' + searchParams)
      .then(res => res.json())
      .then(data => {
        const songs = {}
        for (const songData of data.body) {
          songData['checked'] = true
          songs[songData['song_url']] = songData;
        }
        setSongs(songs)
      })
  }, [searchParams])

  const toggle = (songData) => {
    songData['checked'] = !songData['checked']
    setSongs(prev => ({
      ...prev,
      [songData['song_url']]: songData
    }))
  }

  const buildGraphData = (songData) => {
    var graphData = []
    const totalSyllables = Object.values(songData['line_distribution']).reduce((accumulator, currentValue) => accumulator + Number(currentValue), 0)
    for (var artist of songData['artists']) {
      graphData.push({label: artist['name'], value: Math.floor(songData['line_distribution'][artist['name']] / totalSyllables * 100), color: artist['color']})
    }
    return graphData
  }

  return (
    <div style={{display:'flex'}}>
      <div style={{flex:'1'}}>
        <h1> {groupName} </h1>
        {Object.values(songs).map(songData =>
          <label key={songData['song_name']} style={{display:'block'}} onClick={() => setCurrSong(songData)}>
            <input
              type='checkbox'
              checked={songData['checked']}
              checkedSongNames={songData['checked']}
              onChange={() => toggle(songData)}
            />
            {songData['song_name']}
          </label>
        )}
      </div>
        {Boolean(currSong != "") && <div style={{flex:'1', textAlign:'center'}}>
          <a href={currSong['song_url']} target='_blank'> {currSong['song_name']} </a>
          <PieChart series={[{data: buildGraphData(currSong)}]} width={700} height={700}/>
          <BarChart series={[{data: buildGraphData(currSong)}]} height={700}/>
        </div>}
    </div>
  )
}

export default Group;